export default {
	props: {
		bookInfo: {
			type: Object,
			default() {
				return {}
			}
		}
	},
	methods: {
		nav2bookDetail() {
			this.$emit('nav2bookDetail', this.bookInfo)
		}
	},
	computed: {
		discount() {
			return ((parseInt(this.bookInfo.price) / this.bookInfo.orginPrice)*10).toFixed(1)
		}
	}
}
